import React, { useEffect, useState } from "react";
import {
  useHistory,
  useParams,
} from "react-router-dom/cjs/react-router-dom.min";
import "../css/Detail.scss";

function Detail(props) {
  let [inputData, inputDataChange] = useState([]);
  let [alertShow, alertHide] = useState(true);

  useEffect(() => {
    // 2  
    let timer = setTimeout(() => {
      alertHide(false);
    }, 2000);
    return () => {
      clearTimeout(timer);
    };
  }, []);
  let { id } = useParams();
  let history = useHistory();
  let shoesId = props.shoes.find(function (shoesData) {
    return shoesData.id == id;
  });
  function rmStock() {
    let i = shoesId.id;
    var stockCopy = [...props.stock];
    stockCopy[i] = stockCopy[i] - 1;
    props.stockChange(stockCopy);
  }
  return (
    <div className="container">
      {alertShow === true ? (
        <div className="my-alert-yellow">
          <p>   .</p>
        </div>
      ) : null}
      {inputData}
      <input
        onChange={(e) => {
          inputDataChange(e.target.value);
        }}
      />

      <div className="row">
        <div className="col-md-6">
          <img
            src={
              "https://codingapple1.github.io/shop/shoes" +
              (shoesId.id + 1) +
              ".jpg"
            }
            width="100%"
            alt=""
          />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{shoesId.title}</h4>
          <p>{shoesId.content}</p>
          <p>{shoesId.price}</p>
          <InfoStock stock={props.stock} />
          <button
            className="btn btn-danger"
            onClick={() => {
              rmStock();
            }}
          >
            
          </button>
          <button
            className="btn btn-grey"
            onClick={() => {
              history.goBack();
            }}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
}
function InfoStock(props) {
  return <p> : {props.stock[0]} </p>;
}

export default Detail;
